<script>
export default {
  name: 'ElderList',
  data() {
    return {
      loading: false,
      elderList: []
    };
  },
  mounted() {
    this.fetchElders();
  },
  methods: {
    getDefaultAvatar() {
      // 這是當後端沒照片時顯示的預設圖
      return 'https://images.unsplash.com/photo-1472393365320-dc77242e6ea2?auto=format&fit=crop&w=150&q=80';
    },

    async fetchElders() {
      this.loading = true;
      const baseUrl = 'https://ccu-rfid-project-arhddfhugverf8dr.japanwest-01.azurewebsites.net';
      
      try {
        // 使用相對路徑，讓 main.js 的 baseURL 處理 /manager-api 前綴
        const res = await this.$http.get('/Resident');

        if (res.data && res.data.length > 0) {
          this.elderList = res.data.map(item => {
            // 處理照片網址：如果是相對路徑則補上伺服器網址
            let finalAvatar = null;
            if (item.avatar_url) {
              // 加上時間戳記防止瀏覽器快取舊照片導致顯示空白
              const timestamp = new Date().getTime();
              finalAvatar = item.avatar_url.startsWith('http') 
                ? `${item.avatar_url}?t=${timestamp}`
                : `${baseUrl}${item.avatar_url}?t=${timestamp}`;
            }

            return {
              id: item.id,
              name: item.name,
              age: item.age,
              rfid: item.rfid, // 同步使用後端回傳的 rfid 欄位
              avatar: finalAvatar
            };
          });
        } else {
          this.setMockData();
        }
      } catch (err) {
        console.warn('API 連線失敗，載入展示模式', err);
        this.setMockData(); 
      } finally {
        this.loading = false;
      }
    },
    
    setMockData() {
      this.elderList = [
        { 
          id: 'm1', 
          name: 'James Wilson', 
          age: 82, 
          rfid: '116A2434', 
          avatar: 'https://images.unsplash.com/photo-1529626455594-4ff0802cfb7e?auto=format&fit=crop&w=150&q=80' 
        },
        { 
          id: 'm2', 
          name: '唐伯虎', 
          age: 75, 
          rfid: '8A303053', 
          avatar: 'https://images.unsplash.com/photo-1544144433-d50aff500b91?auto=format&fit=crop&w=150&q=80' 
        }
      ];
    }
  }
};
</script>